body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white; 
  font-weight: 500;
}

.App-editor {
  background-color: #3b465a;
  color: white;
  display: -webkit-flex;
  display: flex;
  width:55%;
  height: 50%;
  min-height: 50vh;
  margin: 0 0 2rem 0;
  font-size: 1rem;
  border: 0.125rem solid;
  border-color: white;
}

.App-preview {
  width:75%;
  height: 50%;
  min-height: 50vh;
  margin: 0 0 5rem;
  font-size: 1rem;
  background-color: #3b465a;
  border: 0.125rem solid;
  border-color: white;
 
}

.App-title {
  display: inline;
  font-size: 2rem;
  font-weight: bold;
  margin: 2rem 0 0 0;
}
